import { environment } from 'src/environments/environment';

/*
Description: Following is used to all constants of API(url)
*/
export class ElesAppConst {
  private static appurl = environment.url;

  public static get APP_URL(): string { return this.appurl; }
  //Store services
  public static get LIST_STORES(): string { return this.appurl + 'store/list'};
  public static get GET_STORE(): string { return this.appurl + 'store/getStore'};
  public static get ADD_STORES(): string { return this.appurl + 'store/add'};
  public static get LIST_UPDATE(): string { return this.appurl + 'store/update'}
  public static get LIST_REMOVE(): string { return this.appurl + 'store/remove'}
  
}
