import { Injectable } from '@angular/core';
import { ReplaySubject } from 'rxjs';
import { JwtHelperService } from '@auth0/angular-jwt';
import { EncriptionDecriptionService } from '../@core/services/encription.decription.service';

/**
  * @Author: Astha
  * @Date  : 20/09/2019
  * Service: HelperService
  * @Description:
  *          -: Create Helper Service to access data in all component
  */
@Injectable({
  providedIn: 'root'
})
export class HelperService {
    public userdata = new ReplaySubject<Array<Object>>();
    public userpermission = new ReplaySubject<Array<Object>>();
    public childpermission = new ReplaySubject<Array<Object>>();
    // public userType = new ReplaySubject<Array<Object>>();
    constructor(public jwtHelper: JwtHelperService, private encriptionDecriptionService: EncriptionDecriptionService) { }
    setUserData(data: any) {
        this.userdata.next(data);
    }

    getUserData() {
        return this.userdata.asObservable();
    }
    setUserPermission(data: any) {
        this.userpermission.next(data);
    }
    getUserPermission() {
        return this.userpermission.asObservable();
    }
    setChildPermission(data: any) {
        this.childpermission.next(data);
    }
    getChildPermission() {
        return this.childpermission.asObservable();
    }
    async isAuthenticated(token) {
        const convertToken = await this.encriptionDecriptionService.decrypt1(token);
        console.log('convert ', convertToken);
        return !this.jwtHelper.isTokenExpired(convertToken.toString());
        // return '';
    }
}
